//
// contribute
//
#contribute {
    @extend %section-top-margin-narrow;
    .wrapper > .section-header {
    	padding: rem(30px) 0 rem(100px);
    } 
    .chart {
	    position: relative;
    	background-color: #fff;
    	.mix-chart {
    		@extend %clearfix;
			background-color: $bg-gray;
			padding: rem(50px) 0;
			width:100vw;
			.chart-inner {
				.section-header {
					height:rem(80px);
					margin-bottom:rem(50px);
					.heading {
						color: $text-color-gray;
						font-size: rem(30px);
						font-weight: 700;
						margin-bottom: rem(20px);
						text-align: center;
					}
					.info-status li {
						padding-bottom: rem(5px);
						color: $text-color-gray;
						font-size:rem(18px);
						line-height:1.26;
						font-weight:300;
						span.kwh1 {
							background: url('data:img/png;base64,')  no-repeat 50% 50%/contain;
							width: rem(20px);
							height: rem(20px);
							display: inline-block;
						}
						span.kwh2 {
							background: url('data:img/png;base64,') no-repeat 50% 50%/contain;
							width: rem(23px);
							height: rem(20px);
							display: inline-block;
						}
					}

				}
        .chart-svg {
          background-image: url('data:img/png;base64,');
          background-position: 0% 50%;
          background-repeat: no-repeat;
          background-size: cover;
          position: relative;
          width: 80vw;
          height: calc(80vw * 304 / 619);
          svg {
            position: absolute;
            width:60vw;
            &#chart-bar {
              left: 12%;
              top: 24%;
            }
            &#point-over {
              left: 6.5%;
              top: -19%;
              opacity: 0;
              width:69vw;
            }
            &#chart-lines {
              left: 11.5%;
              top: -1.5%;
              width:59vw;
            }
            &#chart-point {
              width:57vw;
              left: 13.5%;
              top: -1%;
            }
          }
        }
	    	}
	    }
    	.pie-chart {
    		width: 100vw;
    		height:rem(520px);
    		background: url('../img/contribute/bg-chart-2.jpg') no-repeat 0% 50%/cover;
    		.chart-inner {
    			position: relative;
				.section-header {
					padding: rem(50px) 0 rem(20px);
					.heading {
						color: $text-color-white;
						font-size: rem(30px);
						font-weight: 700;
						margin-bottom: rem(20px);
						text-align: center;
					}
				}
    			.pie-svg {
    				position:relative;
					width:100vw;
					margin:0 auto;
		    		svg { 
		    			position: absolute;
						top:0;right:0;bottom:0;left:0;
						margin:0 auto;
						&#pie {
			    			width:65vw;
						}
						&#textDesc {
			    			width:70vw;
			    			margin: 2vw 0 0 4vw;
						}
		    		}
		    	}
			    .textDesc {
			    	position:absolute;
			    	font-size: rem(23px);
			    	color: $text-color-white;
			    	line-height: 1.26;
			    	text-align: center;
			    	width:60vw;
			    	.numTxt {
			    		font-size: rem(26px);
			    		color: $text-color-white;
			    	}
				    &.textDesc1 {
				    	top:50%;
				    	left:50%;
				    	margin:rem(42px) 0 0 rem(-40px);
				    	color: $text-color-gray;
				    	.numTxt {
				    		color: $text-color-orange;
				    	}	
				    }
			        &.textDesc2 {
				    	top:50%;
				    	left:50%;
				    	margin:rem(-10px) 0 0 rem(50px);	
				    }     
				    &.textDesc3 {
				    	top:50%;
				    	left:50%;
				    	margin:rem(140px) 0 0 rem(-5px);	
				    }     
				    &.textDesc4 {
				    	top:50%;
				    	left:50%;
				    	margin:rem(100px) 0 0 rem(-120px);	
				    }     
				    &.textDesc5 {
				    	top:50%;
				    	left:50%;
				    	margin:rem(5px) 0 0 rem(-140px);	
				    }     
				    &.textDesc6 {
				    	top:50%;
				    	left:50%;
				    	margin:rem(-80px) 0 0 rem(-220px);	
				    }     
				    &.textDesc7 {
				    	top:50%;
				    	left:50%;
				    	margin:rem(-120px) 0 0 rem(-200px);	
				    }
			    }
	    	}
	    }
    	p.text {
    		padding: rem(100px) 0; 
    	}
    }
    .info-graphic {
		background-color: $bg-gray;
		padding: rem(30px) 0 rem(100px);
		text-align:center;
	    .icon {
	    	padding:rem(20px) 0;
	    	margin:0 auto;
	    	svg {
	    		overflow:visible;
	    	}
	    	&.icon-tree {
	    		width:60vw;
	    	}
		    &.icon-space {
		    	width:60vw;

		    }
	    }
	    .sub-header {
	    	display:block;
	    	.title {
	    		font-size: rem(25px);
	    		font-weight: 600;
	    		color: $text-color-gray;
	    		padding:rem(20px) 0 rem(10px);
	    	}
	    	.text {
	    		color: $text-color-gray;
	    	}
	    }
    }
}